/*

Google Code style (c) Aahan Krish <geekpanth3r@gmail.com>

*/
.inlineSchema.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: white;
  color: black; }

.inlineSchema .hljs-comment,
.inlineSchema .hljs-quote {
  color: #800; }

.inlineSchema .hljs-keyword,
.inlineSchema .hljs-selector-tag,
.inlineSchema .hljs-section,
.inlineSchema .hljs-title,
.inlineSchema .hljs-name {
  color: #008; }

.inlineSchema .hljs-variable,
.inlineSchema .hljs-template-variable {
  color: #660; }

.inlineSchema .hljs-string,
.inlineSchema .hljs-selector-attr,
.inlineSchema .hljs-selector-pseudo,
.inlineSchema .hljs-regexp {
  color: #080; }

.inlineSchema .hljs-literal,
.inlineSchema .hljs-symbol,
.inlineSchema .hljs-bullet,
.inlineSchema .hljs-meta,
.inlineSchema .hljs-number,
.inlineSchema .hljs-link {
  color: #066; }

.inlineSchema .hljs-title,
.inlineSchema .hljs-doctag,
.inlineSchema .hljs-type,
.inlineSchema .hljs-attr,
.inlineSchema .hljs-built_in,
.inlineSchema .hljs-builtin-name,
.inlineSchema .hljs-params {
  color: #606; }

.inlineSchema .hljs-attribute,
.inlineSchema .hljs-subst {
  color: #000; }

.inlineSchema .hljs-formula {
  background-color: #eee;
  font-style: italic; }

.inlineSchema .hljs-selector-id,
.inlineSchema .hljs-selector-class {
  color: #9B703F; }

.inlineSchema .hljs-addition {
  background-color: #baeeba; }

.inlineSchema .hljs-deletion {
  background-color: #ffc8bd; }

.inlineSchema .hljs-doctag,
.inlineSchema .hljs-strong {
  font-weight: bold; }

.inlineSchema .hljs-emphasis {
  font-style: italic; }

/*

Sunburst-like style (c) Vasily Polovnyov <vast@whiteants.net>

*/
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #000;
  color: #f8f8f8; }

.hljs-comment,
.hljs-quote {
  color: #aeaeae;
  font-style: italic; }

.hljs-keyword,
.hljs-selector-tag,
.hljs-type {
  color: #e28964; }

.hljs-string {
  color: #65b042; }

.hljs-subst {
  color: #daefa3; }

.hljs-regexp,
.hljs-link {
  color: #e9c062; }

.hljs-title,
.hljs-section,
.hljs-tag,
.hljs-name {
  color: #89bdff; }

.hljs-class .hljs-title,
.hljs-doctag {
  text-decoration: underline; }

.hljs-symbol,
.hljs-bullet,
.hljs-number {
  color: #3387cc; }

.hljs-params,
.hljs-variable,
.hljs-template-variable {
  color: #3e87e3; }

.hljs-attribute {
  color: #cda869; }

.hljs-meta {
  color: #8996a8; }

.hljs-formula {
  background-color: #0e2231;
  color: #f8f8f8;
  font-style: italic; }

.hljs-addition {
  background-color: #253b22;
  color: #f8f8f8; }

.hljs-deletion {
  background-color: #420e09;
  color: #f8f8f8; }

.hljs-selector-class {
  color: #9b703f; }

.hljs-selector-id {
  color: #8b98ab; }

.hljs-emphasis {
  font-style: italic; }

.hljs-strong {
  font-weight: bold; }
