/** specific overrides for bluemix light and dark theme support **/
.apiconnect-explorer .apim-portal-api {
  background: -webkit-linear-gradient(left, white 0%, white 50%, #20343e 50%);
  background: -o-linear-gradient(left, white 0%, white 50%, #20343e 50%);
  background: -webkit-gradient(linear, left top, right top, from(white), color-stop(50%, white), color-stop(50%, #20343e));
  background: linear-gradient(to right, white 0%, white 50%, #20343e 50%); }

.bx--global-light-ui .apiconnect-explorer .apim-portal-api {
  background: -webkit-linear-gradient(left, white 0%, white 50%, #f5f7fa 50%);
  background: -o-linear-gradient(left, white 0%, white 50%, #f5f7fa 50%);
  background: -webkit-gradient(linear, left top, right top, from(white), color-stop(50%, white), color-stop(50%, #f5f7fa));
  background: linear-gradient(to right, white 0%, white 50%, #f5f7fa 50%); }

/* hide stripes when interact collapsed */
.apiconnect-explorer .apim-portal-api.interactNotVisible,
.bx--global-light-ui .apiconnect-explorer .apim-portal-api.interactNotVisible {
  background: -webkit-linear-gradient(left, white 0%, white 50%, white 50%);
  background: -o-linear-gradient(left, white 0%, white 50%, white 50%);
  background: -webkit-gradient(linear, left top, right top, from(white), color-stop(50%, white), color-stop(50%, white));
  background: linear-gradient(to right, white 0%, white 50%, white 50%); }

.apiconnect-explorer input.ng-invalid.ng-touched {
  -webkit-box-shadow: 0 2px 0px 0px #ff5050;
          box-shadow: 0 2px 0px 0px #ff5050; }

.apiconnect-explorer .bx--text__input {
  font-weight: inherit; }

.apiconnect-explorer .bx--search__input {
  color: #8c9ba5; }

.apiconnect-api-explorer .documentation {
  background-color: white; }

.apiconnect-explorer .spinner:after {
  background-color: #20343e; }

.bx--global-light-ui .apiconnect-explorer .spinner:after {
  background-color: #f5f7fa; }

.apiconnect-explorer .documentation .spinner:after,
.bx--global-light-ui .apiconnect-explorer .documentation .spinner:after {
  background-color: #fff; }

.apiconnect-api-explorer a {
  color: #7cc7ff; }

.bx--global-light-ui .apiconnect-api-explorer a {
  color: #3d70b2; }

.apiconnect-api-explorer .underlined:hover {
  border-bottom: 3px solid #7cc7ff; }

.bx--global-light-ui .apiconnect-api-explorer .underlined:hover {
  border-bottom: 3px solid #3d70b2; }

.apiconnect-explorer .interact code,
.apiconnect-explorer .interact code.hljs {
  background-color: rgba(255, 255, 255, 0.1); }

.bx--global-light-ui .apiconnect-explorer .interact code,
.bx--global-light-ui .apiconnect-explorer .interact code.hljs {
  background-color: rgba(61, 112, 178, 0.1); }

.apiconnect-explorer .explorer-toc a.selected,
.apiconnect-explorer .explorer-toc ul .tocItem a:hover {
  background-color: rgba(255, 255, 255, 0.1); }

.bx--global-light-ui .apiconnect-explorer .explorer-toc ul .tocItem a:hover {
  background-color: rgba(61, 112, 178, 0.1); }

.apiconnect-explorer .contentSelectable,
.apiconnect-explorer .explorer-toc .tocToggle {
  color: #7cc7ff; }

.apiconnect-explorer .explorer-toc .tocToggle.tocToggleSelected {
  color: inherit; }

.bx--global-light-ui .apiconnect-explorer .contentSelectable,
.bx--global-light-ui .apiconnect-explorer .explorer-toc .tocToggle {
  color: #3d70b2; }

.bx--global-light-ui .apiconnect-explorer .explorer-toc .tocToggle.tocToggleSelected {
  color: inherit; }

.apiconnect-api-explorer .parametersSection .schema,
.apiconnect-api-explorer .bodySection .schema,
.apiconnect-api-explorer .responsesSection .schema,
.apiconnect-api-explorer .responsesSection .example {
  color: #7cc7ff; }

.bx--global-light-ui .apiconnect-api-explorer .parametersSection .schema,
.bx--global-light-ui .apiconnect-api-explorer .bodySection .schema,
.bx--global-light-ui .apiconnect-api-explorer .responsesSection .schema,
.bx--global-light-ui .apiconnect-api-explorer .responsesSection .example {
  color: #3d70b2; }

.apiconnect-explorer .spinner {
  background: -webkit-linear-gradient(left, #7cc7ff 10%, rgba(255, 255, 255, 0) 42%);
  background: -o-linear-gradient(left, #7cc7ff 10%, rgba(255, 255, 255, 0) 42%);
  background: -webkit-gradient(linear, left top, right top, color-stop(10%, #7cc7ff), color-stop(42%, rgba(255, 255, 255, 0)));
