.bx--global-light-ui .apiconnect-explorer button.iconOnlyButton:not(:disabled):hover,
.bx--global-light-ui .apiconnect-explorer button.iconOnlyButton:not(:disabled):focus {
  color: #3d70b2; }

.apiconnect-api-explorer .langs-menu li {
  background-color: rgba(255, 255, 255, 0.1); }

.bx--global-light-ui .apiconnect-api-explorer .langs-menu li {
