.bx--global-light-ui .apiconnect-api-explorer .interact .responseSection .request,
.bx--global-light-ui .apiconnect-api-explorer .interact .responseSection .response {
  background-color: rgba(61, 112, 178, 0.1); }

/* Tweaks for the menu filter button */
.apiconnect-explorer .bx--toolbar.searchAndFilter {
  margin-top: 11px; }

.apiconnect-explorer .bx--overflow-menu__icon {
