@-webkit-keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.apiconnect-copier {
  position: relative;
  display: none; }

*:hover > .apiconnect-copier {
  display: block; }

.apiconnect-copier .copyNotifier {
  display: none;
  position: absolute;
  right: 35px;
  top: 5px;
  border: 1px solid #dfe6eb;
  background: white;
  padding: 2px 5px; }

.apiconnect-copier .copyNotifier.notify {
  display: inherit;
  -webkit-animation: fadeIn 1s;
          animation: fadeIn 1s; }

.apiconnect-copier .copy-icon {
  position: absolute;
  right: 10px;
  top: 10px;
  height: 1rem;
  width: 1rem;
  vertical-align: middle;
  margin-left: 5px;
  cursor: pointer; }
